

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import cookies from 'js-cookie'
import subjectModel  from '../lib/subjectModel'
import util  from '../lib/util'
import moment  from 'moment'
export default {
components: {
Group,
Cell,
Tabbar,
TabbarItem
},
name: 'index',
data () {
return {
today_course:'',
calendar_subject:'math',
home:true,
calendar:false,
my:false
}
},
watch: {
"$route"(to,from){

if(to.name == 'home'){
this.home = true;
this.calendar = false;
this.my = false;
}
}
},
mounted :function(){

},
computed: {
key() {
return new Date().getTime();
}
},


activated:function(){

this.today_course = '/index/home/'+cookies.get('subject')+'/'+moment().format('YYYY-MM-DD');
this.calendar_subject = '/index/calendar/'+cookies.get('subject');
}
}



