import { sellerRegister, getShareImg } from '@/api/index'
import { EventBus } from "../bus/event-bus.js";
import ButtonFooter from '@/components/buttonFooter'
import html2canvas from "html2canvas"
import QRCode from 'qrcodejs2'  // 引入qrcode
export default {
  components: {
    ButtonFooter
  },
  data () {
      return {
        form:{
          weibo: '',
          zhihu: '',
          douyin: '',
          weixin: '',
          kuaishou: '',
          wangyi: ''
        },
        qrcode: '',
        sign: {},
        genders:  [['男', '女']],
        toast: false,
        qq: null,
        show_form: true,
        show_qrcode: false,
        show_down_load: false
      }
  },
  mounted:function(){
  },
  // 组件被激活时调用
  activated:function(){
    this.show_form = true
    this.show_qrcode = false
    this.show_down_load = false
    
    // this.qrcode="http://guichaokeji.com/bird-api/images/v2/15101056160.jpeg"
    // this.show_form = false
    // this.show_qrcode = false
    // this.show_down_load = true
    if(localStorage.getItem('userAccounts')) {
      this.form = JSON.parse(localStorage.getItem('userAccounts'))
    }
    // 
  },
  methods:{
    clickDown() {
      this.$vux.toast.show({
       text: '长按图片保存',
       type: 'text'
      })
    },

    // 利用插生成二维码
    // makeQrcode(url) {
    //   this.show_qrcode = true
    //   setTimeout(() => {
    //     let qrcode = new QRCode('qrcode', {
    //       width: 80,  
    //       height: 80,
    //       text: url, // 二维码地址
    //       colorDark : "#000",
    //       colorLight : "#fff",
    //     })
    //     this.downLoad()
    //   }, 200)
    // },
    
    // 使用canvas生成base64图片显示出来
    downLoad() {
      let _this = this;
      // this.show_down_load = true
      // this.show_qrcode = false
      // this.$vux.loading.hide()
      // 声明一个画布元素,存储需下载图片范围
      
      // 获取需下载范围元素
      let img = this.$refs["QRcodeSrcImg"];
      // 图片高度
      var w = img.offsetWidth;
      // 图片宽度
      var h = img.offsetHeight
      console.log(w)
      console.log(h)
      // 声明画布宽高
      let canvas = document.createElement("canvas");
      let context = canvas.getContext("2d");
      let scale = parseInt(this.getPixelRatio(context)); 
      console.log(scale)
      canvas.width = w * scale
      canvas.height = h * scale
      canvas.style.width = w + "px";
      canvas.style.height = h + "px";
      //context.scale(scale, scale); // 这行代码不能加，不知道为什么我会有这行

      // 利用 html2canvas 下载 canvas
      var opts = {
            scale: scale, 
            canvas: canvas,
            width: w, 
            height: h,
            dpi: window.devicePixelRatio,
            useCORS: true, // 【重要】开启跨域配置
            allowTaint: true,//允许跨域图片
      }
      html2canvas(img,opts).then(function(canvas) {
        _this.qrcode = canvas.toDataURL('image/png', 1.0)
        _this.show_down_load = true
        
      });
    },
    
    // 获取设备的PixelRatio
    getPixelRatio(context) {     
            var backingStore = context.backingStorePixelRatio ||
                context.webkitBackingStorePixelRatio ||
                context.mozBackingStorePixelRatio ||
                context.msBackingStorePixelRatio ||
                context.oBackingStorePixelRatio ||
                context.backingStorePixelRatio || 1;
            return (window.devicePixelRatio || 1) / backingStore;
    },

    // 检查信息
    checkQrcode() {
      this.downLoad()
      return false
      // this.show_form = false
      // this.$vux.loading.show({
      //  text: '生成中···'
      // })

      // Sentry.setExtra('data', this.form)
      // Sentry.captureMessage('查询二维码','info');
      localStorage.setItem('userAccounts', JSON.stringify(this.form))
      
      this.form.stamp = new Date().getTime();

      getShareImg(this.form).then(data => { 
        console.log(data)
        this.qrcode = data
        this.show_form = false
        this.show_down_load = true
        this.$vux.loading.hide()
      })
   },
  },

  events: {
  }

}



